import { useState } from 'react';
import styled, { keyframes } from 'styled-components';
import { ANIMATION_DURATION } from '../../constants';
import { ModalWrapper } from '../shared/modal-wrapper';
import { HighlightedText, TextDivider, TextMd, UnderlinedText } from '../shared/texts';
import { FlexWrapper } from '../shared/flex-wrapper';

const Wrapper = styled(ModalWrapper)`
  z-index: 98;
`;

const RulesWrapper = styled(FlexWrapper)`
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  animation: ${({$isClosing}) => $isClosing ? disappear : appear } ${ANIMATION_DURATION}ms ease-in-out both; 
`;

const appear = keyframes`
  0% {
    bottom: -100vh;
  }

  100% {
    bottom: 0;
  }
`;

const disappear = keyframes`
  0% {
    bottom: 0;
  }

  100% {
    bottom: -100vh;
  }
`;

const CloseRect = styled.div`
  background: white;
  width: 58px;
  height: 6px;
  border-radius: 20px;
  margin: 0 auto 8px;
  cursor: pointer;
`;

const CloseRectWrapper = styled.div`
  padding: 15px 20px 0;
`;

const Content = styled.div`
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 25px 25px 0 0;
  background: white;
  padding: min(33px, 8.8vw) min(20px, 5.3%) min(40px, 10vw);
  width: 89.4%;
`;

export const Rules = ({onClose}) => {
    const [isClosing, setIsClosing] = useState(false);

    const handleClose = () => {
        if (isClosing) return;
        setIsClosing(true);
        setTimeout(onClose, ANIMATION_DURATION);
    };

    return (
        <Wrapper>
            <RulesWrapper $isClosing={isClosing}>
                <CloseRectWrapper onClick={handleClose} onMouseDown={handleClose} onTouchStart={handleClose}>
                    <CloseRect />
                </CloseRectWrapper>
                <Content>
                    <TextMd>
                        <HighlightedText $isFirstWord color='green'></HighlightedText>  ,
                          {'\u00A0'}    {'\u00A0'}{'\u00A0'}.
                        <TextDivider/>
                        {'\u00A0'}<HighlightedText color='purple'> </HighlightedText>?{'\n'}
                           <UnderlinedText color='blue'></UnderlinedText>, 
                           .
                        <TextDivider/>
                        <UnderlinedText color='blue'>
                              {'\u00A0'},  {'\u00A0'} {'\u00A0'} .
                        </UnderlinedText>  {'\u00A0'} ,  ,
                              {'\u00A0'} .
                    </TextMd>
                </Content>
            </RulesWrapper>
        </Wrapper>
    );
};
